import app from '@/common/app.js'; //公共变量
import request from '@/common/request.js';
import expand from '@/common/expand.js';
import {
    mapState
} from 'vuex';
export default {
    data() {
        return {
            serverPath: app.imagePath,
            num: 0,
            school_name: "",
            tabList: ['次数', '金额'],
            cardId: "",
            ranking: [], //前三名排名列表
            order: [] // 下面的顺序列表
        }
    },
    onLoad(options) {
        //获取cardId
        if (options.cardId != undefined) {
            this.cardId = decodeURIComponent(options.cardId);
        }
    },
    computed: {
        ...mapState(['configParams'])
    },
    onShow() {
        this.school_name = this.configParams.school_name;
        //获取排名
        request.http({
            url: '/app/ranking/getCard',
            method: "POST",
            data: {
                cardId: this.cardId,
            },
            header: 'application/x-www-form-urlencoded',
            success: (res) => {
                if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                    this.ranking = res.data.data.ranking;
                    this.order = res.data.data.order;
                }
            },
            error: (res) => {

            }
        });
    },
    onShareAppMessage(res) {
        console.log(this.school_name);
        expand.saveShare(app.share_code.CARDRANK_SHARE);
        return {
            title: this.school_name + '领卡排名',
            path: '/pages_mine/myCard/cardRank?cardId=' + this.cardId
        }
    },
    methods: {
        getNum(index) {
            this.num = index;
        }
    }
}
